// import { createRouter, createWebHistory } from 'vue-router'
import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    component: () => import('@/views/website'),
    children: [
      {
          path: '/',
          name: 'home',
          component: () => import('@/views/website/pages/home')
      },
      {
        path: '/quotation',
        name: 'quotation',
        component: () => import('@/views/website/pages/quotation')
      },
      // {
      //     path: '/about',
      //     name: 'about',
      //     component: () => import('@/views/About.vue')
      // }
    ]
  },
  {
    // 測試頁
    path: '/test',
    name: 'test',
    props: true,
    component: () => import('@/views/test'),
  },
  {
    // 筆記
    path: '/note',
    name: 'Note',
    props: true,
    component: () => import('@/views/note'),
    children: [
      {
        path: '/note',
        name: 'noteTools',
        props: true,
        component: () => import('@/views/tool'),
      },
      {
        path: '/note',
        name: 'noteQuotation',
        props: true,
        component: () => import('@/views/website/pages/quotation'),
      }
    ]
  },
  {
    // 說明手冊
    path: '/handbook',
    name: 'Handbook',
    props: true,
    component: () => import('@/views/handbook'),
  },
  {
    // css helpers 說明手冊
    path: '/cssHelpers',
    name: 'CssHelpers',
    props: true,
    component: () => import('@/views/cssHelpers'),
  },
  {
    // 展示頁
    path: '/demo',
    name: 'Demo',
    props: true,
    component: () => import('@/views/demo'),
    children: [
      {
        path: '/demo',
        name: 'modern-button',
        props: true,
        component: () => import('@/views/demo/page/modern-button')
      },
    ]
  },
  // Tool
  {
    path: '/tools',
    name: 'Tools',
    props: true,
    component: () => import('@/views/tool'),
  },
  {
    path: '/WhatIsMyViewport',
    name: 'WhatIsMyViewport',
    props: true,
    component: () => import('@/views/tool/what-is-my-viewport'),
  },
  {
    path: '/DemoFontSize',
    name: 'DemoFontSize',
    props: true,
    component: () => import('@/views/tool/font-size'),
  },
  {
    path: '/unit',
    name: 'UnitConversion',
    props: true,
    component: () => import('@/views/tool/unit'),
  },
  {
    path: '/nth-child-tester',
    name: 'nth-child-tester',
    props: true,
    component: () => import('@/views/tool/nth-child-tester'),
  },
  {
    path: '/cocktails',
    name: 'Cocktails',
    props: true,
    component: () => import('@/views/tool/cocktails'),
  },
  {
    path: '/:pathMatch(.*)',
    component: () => import('@/views/not-found'),
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

// router.beforeEach((to, from, next) => {
//   console.log('router to : ', to)
//   console.log('router from : ', from)

//   next()
// })

export default router;
